import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Vendor } from '../vendor/vendor-model';
import { VendorServiceService } from '../vendor/vendor-service.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-view-contact',
  templateUrl: './view-contact.component.html',
  styleUrls: ['./view-contact.component.scss']
})
export class ViewContactComponent implements OnInit {

  constructor(private router :Router,private service:VendorServiceService,private route:ActivatedRoute) { }

  ngOnInit() {
    this.getVendorById();
    this.resetform();
  }
  vendor_id:any;
  vendor= new Vendor();


  resetform(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={ 
    vendor_id:null,
    vendor_name:'',
    address:'',
    location:null,
    service:'',
    pincode:'',
    isActive:'',
    cont_id:null,
    name:'',
    dept:'',
    email:'',
    phone:''                      
    }                                  
  }

  // navigate to vendor page
  back(){
    this.router.navigate(['vendor'])
  }

  // navigate to login page

  logout(){
    this.router.navigate(['login'])
  }

  getVendorById() {
    this.resetform();
    this.vendor_id =this.route.snapshot.paramMap.get('id');
    this.service.getVendorById(this.vendor_id)
    // console.log("Vendor Id " + vendor_id);
      .subscribe((data) => {
        this.vendor = data;
        console.log("service"+data);
      }, (error) => {
        console.log(error);
      });
  }

}
