import { Component, OnInit } from '@angular/core';
import { SaveVendorServiceService } from './save-vendor-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';
import { Vendor } from '../vendor/vendor-model';

@Component({
  selector: 'app-save-vendor',
  templateUrl: './save-vendor.component.html',
  styleUrls: ['./save-vendor.component.scss']
})
export class SaveVendorComponent implements OnInit {

  constructor(private service : SaveVendorServiceService,private router:Router,private route: ActivatedRoute) { }
  vendor= new Vendor();
  vendors:Vendor[];
  vendor_id: any;

  ngOnInit() {
    this.route.params.subscribe(params => this.getVendorbyId(params['id']));
    this.resetForm();
  }

  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={ 
    vendor_id:null,
    vendor_name:'',
    address:'',
    location:null,
    service:'',
    pincode:'',
    isActive:'',
    cont_id:null,
    name:'',
    dept:'',
    email:'',
    phone:''                      
    }                                  
  }

 // insert new vendor

  addVendor():void{
    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor;
      this.router.navigate(['vendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }

  // to get all vendors

  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }

  // navigate to vendor page

  viewVendor(){
    this.router.navigate(['vendor'])
  }
  getVendorbyId(vendor_id: any) {
    this.vendor_id = vendor_id;
    console.log("Vendor Id " + vendor_id);
    this.service.getVendorbyId(vendor_id)
      .subscribe((searchData) => {
        this.vendor = searchData;
        console.log("inside"+ searchData);
      }, (error) => {
        console.log(error);
      });
  }

  // navigate to vendor page

  back(){
    this.router.navigate(['vendor'])
  }
  
  // for logging out

  logout(){
    this.router.navigate(['login'])
  }

}
